.iconCont {
	height: 100%;
	z-index: 10;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	padding: 0 15%;
	margin-top: -400px;
}

.icons {
	width: 25%;
}

.icons img {
	margin-bottom: 40px;
}

.icons img:hover {
	transform: scale(1.05);
	transition: 200ms ease-in-out;
}