.footer {
	height: 200px;
	background: #0D5C63;
	color: white;
	-webkit-clip-path: polygon(0 0, 100% 50%, 100% 100%, 0% 100%);
	clip-path: polygon(0 0, 100% 50%, 100% 100%, 0% 100%);
	display: flex;
	justify-content: space-around;
	align-items: center;
	padding: 0 20%;
	margin-top: 100px;
	padding-top: 100px;
}

@media (max-width: 768px) {
.footer {
	height: 135px;
	bottom: 0;
	padding: 0 5%;
	position: fixed;
	width: 100%;
	left: 0;
	text-align: center;
		display: none ;
}
.footer img {
margin: 0 auto;
height: 20px;
}

.footer .sutb {
	height: 35px;
}

}